.section-sub{
	background-color: #3F5D45;
	height: 186px;
	width: 100%;
	display: -webkit-box;
	display: -ms-flexbox;
	display: flex;
	-webkit-box-orient: vertical;
	-webkit-box-direction: normal;
	    -ms-flex-direction: column;
	        flex-direction: column;
	-ms-align-items: center;
	-webkit-box-align: center;
	    -ms-flex-align: center;
	        align-items: center;
	-webkit-box-pack: center;
	    -ms-flex-pack: center;
	        justify-content: center;
}
@media (min-width: 1024px){
	.section-sub{
		-webkit-box-orient: horizontal;
		-webkit-box-direction: normal;
		    -ms-flex-direction: row;
		        flex-direction: row;
	}
}
.section-sub .panel-top{
	display: -moz-flex;
	display: -ms-flex;
	display: -o-flex;
	display: -webkit-box;
	display: -ms-flexbox;
	display: flex;
	-ms-align-items: center;
	-webkit-box-align: center;
	    -ms-flex-align: center;
	        align-items: center;
	-webkit-box-pack: center;
	    -ms-flex-pack: center;
	        justify-content: center;
	margin-bottom: 23px;
}
@media (min-width: 1024px){
	.section-sub .panel-top{
		margin-bottom: 0;
		margin-right: 124px;
	}
}
.section-sub .panel-top .logo{
	width: 40px;
	height: 40px;
	margin-right: 20px;
}
.section-sub .panel-top .logo img{
	width: 100%;
	height: 100%;
}
.section-sub .panel-top span{
	color: #fff;
	font-size: 24px;
	line-height: 29px;
	font-weight: 300;
	letter-spacing: 2px;
}
.section-sub .panel-bot{
	width: calc(100% - 60px);
	display: -webkit-box;
	display: -ms-flexbox;
	display: flex;
	position: relative;
}
@media (min-width: 768px){
	.section-sub .panel-bot{
		width: calc(100% - 100px);
	}
}
@media (min-width: 1024px){
	.section-sub .panel-bot{
		width: 380px;
	}
}
.section-sub .panel-bot .icon{
	position: absolute;
	left: 20px;
	top: 50%;
	-webkit-transform: translateY(-50%);
	        transform: translateY(-50%);
}
.section-sub .panel-bot .icon i{
	font-size: 20px;
	color: #3F5D45;
}
.section-sub .panel-bot input{
	background-color: #EAF0ED;
	height: 56px;
	width: 100%;
	padding-left: 64px;
}
@media (min-width: 1024px){
	.section-sub .panel-bot input{
		width: 316px;
	}
}
.section-sub .panel-bot .submit{
	position: relative;
	background-color: #FFE180;
	width: 64px;
	cursor: pointer;
	overflow: hidden;
	position: relative;
	z-index: 10;
}
.section-sub .panel-bot .submit:after{
	content: '';
	background-color: #FFE180;
	width: 100%;
	height: 100%;
	position: absolute;
	top: 0;
	left: 0;
	z-index: -1;
}
.section-sub .panel-bot .submit .arrow{
	position: absolute;
	left: 50%;
	top: 50%;
	-webkit-transform: translate(-50%, -50%);
	        transform: translate(-50%, -50%);
}
.section-sub .panel-bot .submit .arrow i{
	font-size: 20px;
	color: #3F5D45;
}
footer{
	background-color: #EAF0ED;
	width: 100%
}
footer .footer-content{
	padding: 30px;
	display: -webkit-box;
	display: -ms-flexbox;
	display: flex;
	-webkit-box-orient: vertical;
	-webkit-box-direction: normal;
	    -ms-flex-direction: column;
	        flex-direction: column;
	margin: 0 auto;
}
@media (min-width: 768px){
	footer .footer-content{
		padding: 50px;
	}
}
@media (min-width: 1024px){
	footer .footer-content{
		width: 1024px;
		padding: 40px 113px;
		-webkit-box-orient: horizontal;
		-webkit-box-direction: normal;
		    -ms-flex-direction: row;
		        flex-direction: row;
		-webkit-box-pack: justify;
		    -ms-flex-pack: justify;
		        justify-content: space-between;
	}
}
footer .footer-content p{
	color: #3F5D45;
	font-size: 16px;
	line-height: 24px;
}
footer .footer-content .panel-left{
	position: relative;
}
footer .footer-content .panel-left .logo{
	width: 171px;
	margin-bottom: 30px;
}
@media (min-width: 1024px){
	footer .footer-content .panel-left .logo{
		margin-bottom: 120px;
	}
}
footer .footer-content .panel-left .logo img{
	width: 100%;
}
footer .footer-content .panel-left .contact{
	display: -webkit-box;
	display: -ms-flexbox;
	display: flex;
	margin-top: 30px;
	margin-bottom: 25px;
}
@media (min-width: 1024px){
	footer .footer-content .panel-left .contact{
		position: absolute;
		bottom: 0;
		margin: 0;
	}
}
footer .footer-content .panel-left .contact .icon{
	width: 32px;
	height: 32px;
	margin-right: 8px;
}
footer .footer-content .panel-left .contact .icon img{
	width: 100%;
	height: 100%;
}
footer .footer-content .panel-right{
	display: -moz-flex;
	display: -ms-flex;
	display: -o-flex;
	display: -webkit-box;
	display: -ms-flexbox;
	display: flex;
	-webkit-box-orient: vertical;
	-webkit-box-direction: normal;
	    -ms-flex-direction: column;
	        flex-direction: column;
}
@media (min-width: 1024px){
	footer .footer-content .panel-right img{
		width: 60px;
		-ms-flex-item-align: end;
		    align-self: flex-end;
		margin-bottom: 45px;
	}
}
footer .footer-content .panel-right .copyright{
	font-size: 16px;
	font-family: 'Lora', serif;
}
.desktop .section-sub .panel-bot:hover .submit .arrow{
	-webkit-animation: forward .4s;
	        animation: forward .4s;
}
@-webkit-keyframes forward{
	from{
		-webkit-transform: translate(-500%, -50%);
		        transform: translate(-500%, -50%);
		-webkit-transition: .2s;
		transition: .2s;
	}
	to{
		-webkit-transform: translate(-50%, -50%);
		        transform: translate(-50%, -50%);
		-webkit-transition: unset;
		transition: unset;
	}
}
@keyframes forward{
	from{
		-webkit-transform: translate(-500%, -50%);
		        transform: translate(-500%, -50%);
		-webkit-transition: .2s;
		transition: .2s;
	}
	to{
		-webkit-transform: translate(-50%, -50%);
		        transform: translate(-50%, -50%);
		-webkit-transition: unset;
		transition: unset;
	}
}



