    .page-cart .section-content .paid .panel-top .total{
        width: calc(100% - 36px);
    }
}
.page-cart .section-content .paid .panel-top .total span{
    font-size: 20px;
    color: #3F5D45;
    font-weight: bold;
}
@media (min-width: 768px){
    .page-cart .section-content .paid .panel-top .total span{
        color: #fff;
    }
}
.page-cart .section-content .paid .panel-bottom{
    width: 100%;
    height: 65px;
    background-color: #FFE180;
