.stuff{
	width: calc(100% - 66px);
	margin: 0 auto;
	margin-bottom: 30px;
}


@media (min-width: 768px){


	.stuff{
		width: calc(50% - 50px);
		margin: 0;
		margin-right: 20px;
	}
}


.stuff .stuff-img{
	width: 100%;
	height: 315px;
	position: relative;
	overflow: hidden;
}


.stuff .stuff-img .img{
	width: 100%;
	height: 100%;
	background-size: cover;
	position: absolute;
	top: 0;
	left: 0;
	-webkit-transition: .5s;
	transition: .5s;
}


.stuff .stuff-img .todaypick{
	width: 35px;
	height: 105px;
	background-color: #3F5D45;
	display: -moz-flex;
	display: -ms-flex;
	display: -o-flex;
	display: -webkit-box;
	display: -ms-flexbox;
	display: flex;
	-webkit-box-pack: center;
	    -ms-flex-pack: center;
	        justify-content: center;
	-ms-align-items: center;
	-webkit-box-align: center;
	    -ms-flex-align: center;
	        align-items: center;
	position: absolute;
	left: 21px;
}


.stuff .stuff-img .todaypick span{
	color: #fff;
	-webkit-writing-mode: vertical-lr;
	    -ms-writing-mode: tb-lr;
	        writing-mode: vertical-lr;
	font-size: 16px;
	font-weight: 300;
	letter-spacing: 2px;
}


.stuff .stuff-img .like{
	position: absolute;
	right: 24px;
	top: 20px;
	cursor: pointer;
}


.stuff .stuff-img .like .fa-heart{
	font-size: 20px;
	color: #3F5D45;
	-webkit-transition: .2s;
	transition: .2s;
}


.stuff .stuff-img .like .fas{
	color: #ff4f66;
	-webkit-animation: heartBigger 1s;
	        animation: heartBigger 1s;
}


.stuff .stuff-title{
	width: 100%;
	border-collapse: collapse;
}


.stuff .stuff-title span{
	font-size: 20px;
	color: #3F5D45;
	font-weight: 300;
	border-left: 1px solid #EAF0ED;
	border-right: 1px solid #EAF0ED;
	width: 60%;
	display: inline-block;
	text-align: center;
	padding: 13px 0;
}


.stuff .stuff-title .price{
	font-weight: bold;
	width: 40%;
}


.stuff .btn{
	background-color: #EAF0ED;
	width: 100%;
	height: 65px;
	font-size: 24px;
	line-height: 65px;
	text-align: center;
	color: #3F5D45;
	cursor: pointer;
	position: relative;
	-webkit-transition: .1s;
	transition: .1s;
	z-index: 10;
}


.stuff .btn:after{
	content: '';
	-webkit-transition: .3s ease-in-out;
	transition: .3s ease-in-out;
	background-color: #3F5D45;
	width: 0;
	height: 100%;
	position: absolute;
	top: 0;
	left: 0;
	z-index: -1;
}


.desktop .stuff .stuff-img:hover .img{
	-webkit-transform: scale(1.2);
	        transform: scale(1.2);
	-webkit-transition: .5s;
	transition: .5s;
}


.desktop .stuff .btn:hover{
	color: #fff;
	-webkit-transition: .1s;
	transition: .1s;
}


.desktop .stuff .btn:hover:after{
	width: 100%;
	-webkit-transition: .3s ease-in-out;
	transition: .3s ease-in-out;
}


@-webkit-keyframes heartBigger{
	from{
		-webkit-transform: scale(1.2);
		        transform: scale(1.2);
	}
	to{
		-webkit-transform: scale(1);
		        transform: scale(1);
	}
}


@keyframes heartBigger{
	from{
		-webkit-transform: scale(1.2);
		        transform: scale(1.2);
	}
	to{
		-webkit-transform: scale(1);
		        transform: scale(1);
	}
}


