.page-index .section-index .option li span{
	-webkit-transition: .5s;
	transition: .5s;

}
.page-index .section-index .option li:before{
	content: '';
	width: 100%;
	height: 100%;
	display: block;
	position: absolute;
	left: 0;
	top: 0;
	opacity: .8;
	background-color: #3F5D45;
	-webkit-transition: .5s;
	transition: .5s;

}
.page-index .section-index .option li img{
	width: 100%;
	height: 100%;
	position: absolute;
	top: 0;
	left: 0;
	filter: url('data:image/svg+xml;charset=utf-8,<svg xmlns="http://www.w3.org/2000/svg"><filter id="filter"><feGaussianBlur stdDeviation="5" /></filter></svg>#filter');
	-webkit-filter: blur(5px);
	        filter: blur(5px);
	z-index: -1;

}
.page-index .section-index .option li span{
	color: #fff;
	-webkit-writing-mode: vertical-lr;
	    -ms-writing-mode: tb-lr;
	        writing-mode: vertical-lr;
	font-size: 20px;
	line-height: 24px;
	z-index: 10;
	position: absolute;
	top: 50%;
	left: 50%;
	-webkit-transform: translate(-50%, -50%);
	        transform: translate(-50%, -50%);
	font-weight: 300;
	letter-spacing: 2px;

}
.page-index .section-index .option li:nth-child(2){
	border-left: 1px solid #fff;
	border-right: 1px solid #fff;

}
.page-index .section-index .option li:hover span{
	color: #3F5D45;
	-webkit-transition: .5s;
	transition: .5s;

}
.page-index .section-index .option li:hover:before{
	background-color: #FFE180;
	-webkit-transition: .5s;
	transition: .5s;

}
