.page-product{
	position: relative
}
.page-product .section-cover{
	position: relative;
}
@media (min-width: 1024px){
	.page-product .section-cover{
		width: 940px;
		height: 496px;
		margin: 0 auto 60px auto
	}
}
.page-product .section-cover .cover{
	width: 100%;
	height: 277px;
	background-size: cover;
}
@media (min-width: 768px){
	.page-product .section-cover .cover{
		height: 380px
	}
}
@media (min-width: 1024px){
	.page-product .section-cover .cover{
		height: 496px
	}
}
.page-product .section-cover .titletext{
	width: 60px;
	position: absolute;
	right: 30px;
	top: 30px;
}
@media (min-width: 1024px){
	.page-product .section-cover .titletext{
		width: 89px;
		right: 40px;
		top: 40px
	}
}
.page-product .section-cover .titletext img{
	width: 100%;
}
.page-product .section-content{
	margin-bottom: 30px;
}
@media (min-width: 1024px){
	.page-product .section-content{
		display: -webkit-box;
		display: -ms-flexbox;
		display: flex;
		-webkit-box-pack: center;
		    -ms-flex-pack: center;
		        justify-content: center;
		width: 940px;
		margin: 0 auto 60px auto
	}
}
.page-product .section-content .navi{
	margin-bottom: 30px;
	border-collapse: collapse;
}
@media (min-width: 1024px){
	.page-product .section-content .navi{
		width: 300px
	}
}
.page-product .section-content .navi li{
	list-style: none;
	width: 100%;
	height: 65px;
	text-align: center;
	border: 1px solid #EAF0ED;
	cursor: pointer
}
.page-product .section-content .navi li span{
	font-size: 20px;
	line-height: 65px;
	letter-spacing: .5px;
	color: #3F5D45;
	font-weight: bold;
}
.page-product .section-content .navi li:hover{
	background-color: #EAF0ED;
}
.page-product .section-content .navi li:first-of-type{
	background-color: #3F5D45
}
.page-product .section-content .navi li:first-of-type span{
	color: #fff;
}
.page-product .section-content .stuff-wrapper .stuff-content{
	display: -webkit-box;
	display: -ms-flexbox;
	display: flex;
	-webkit-box-orient: vertical;
	-webkit-box-direction: normal;
	    -ms-flex-direction: column;
	        flex-direction: column;
	-ms-align-items: center;
	-webkit-box-align: center;
	    -ms-flex-align: center;
	        align-items: center;
	-webkit-box-pack: center;
	    -ms-flex-pack: center;
	        justify-content: center;
}
@media (min-width: 768px){
	.page-product .section-content .stuff-wrapper .stuff-content{
		-webkit-box-orient: horizontal;
		-webkit-box-direction: normal;
		    -ms-flex-direction: row;
		        flex-direction: row;
		-ms-flex-wrap: wrap;
		    flex-wrap: wrap
	}
}
@media (min-width: 1024px){
	.page-product .section-content .stuff-wrapper .stuff-content{
		width: 640px;
		-webkit-box-pack: end;
		    -ms-flex-pack: end;
		        justify-content: flex-end
	}
}
.page-product .section-content .stuff-wrapper .stuff-content .stuff{
	margin: 0;
	margin-bottom: 30px;
}
@media (min-width: 768px){
	.page-product .section-content .stuff-wrapper .stuff-content .stuff{
		margin-right: 25px
	}
}
@media (min-width: 1024px){
	.page-product .section-content .stuff-wrapper .stuff-content .stuff{
		width: 300px
	}
}
.page-product .section-content .stuff-wrapper .stuff-content .stuff:nth-child(even){
	margin-right: 0;
}
.page-product .section-content .stuff-wrapper .pagenum{
	display: -moz-flex;
	display: -ms-flex;
	display: -o-flex;
	display: -webkit-box;
	display: -ms-flexbox;
	display: flex;
	-ms-align-items: center;
	-webkit-box-align: center;
	    -ms-flex-align: center;
	        align-items: center;
	-webkit-box-pack: center;
	    -ms-flex-pack: center;
	        justify-content: center;
}
@media (min-width: 1024px){
	.page-product .section-content .stuff-wrapper .pagenum{
		-webkit-box-pack: end;
		    -ms-flex-pack: end;
		        justify-content: flex-end
	}
}
.page-product .section-content .stuff-wrapper .pagenum .num{
	width: 60px;
	height: 60px;
	text-align: center;
	border: 1px solid #EAF0ED;
	cursor: pointer
}
.page-product .section-content .stuff-wrapper .pagenum .num span,.page-product .section-content .stuff-wrapper .pagenum .num i{
	font-size: 20px;
	line-height: 60px;
	color: #3F5D45;
}
.page-product .section-content .stuff-wrapper .pagenum .num:hover{
	background-color: #EAF0ED;
}
.page-product .section-content .stuff-wrapper .pagenum .prev i{
	-webkit-transform: rotate(-90deg) translateY(4px);
	        transform: rotate(-90deg) translateY(4px);
}
.page-product .section-content .stuff-wrapper .pagenum .next i{
	-webkit-transform: rotate(90deg) translateY(4px);
	        transform: rotate(90deg) translateY(4px);
}
.page-product .section-content .stuff-wrapper .pagenum .now{
	background-color: #3F5D45
}
.page-product .section-content .stuff-wrapper .pagenum .now span{
	color: #fff;
}


