.header {
    position: fixed;
    z-index: 100;
    width: 100%;
    height: 80px;
}

@media (min-width: 1024px) {

    .header {
        padding: 30px 40px;
        height: 100px;
        background-color: #fff;
    }
}

.fa-shopping-cart{
    color: #3F5D45;
}

.mobile-menu{
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-pack: justify;
        -ms-flex-pack: justify;
            justify-content: space-between;
    -webkit-box-align: center;
        -ms-flex-align: center;
            align-items: center;
    z-index: 10;
    background-color: #fff;
    width: 100%;
    height: 100%;
    padding: 30px;
}

@media (min-width: 768px) {

    .mobile-menu {
        padding: 30px 50px;
    }
}

@media (min-width: 1024px) {

    .mobile-menu {
        display: none;
    }
}

.mobile-menu .hamberger {
    height: 12px;
}

.mobile-menu .hamberger span {
    display: block;
    width: 18px;
    height: 2px;
    background-color: #3F5D45;
    position: relative;
    -webkit-transition: .2s;
    transition: .2s;
}

.mobile-menu .hamberger span:nth-child(2) {
    top: 5px;
    -webkit-transition: .5s;
    transition: .5s;
}

.mobile-menu .hamberger span:last-child {
    top: 10px;
    -webkit-transition: .5s;
    transition: .5s;
}

.mobile-menu .hamberger.open span:first-child {
    width: 0;
}

.mobile-menu .hamberger.open span:nth-child(2) {
    -webkit-transform: rotate(45deg);
            transform: rotate(45deg);
}

.mobile-menu .hamberger.open span:last-child {
    -webkit-transform: rotate(-45deg) translate(5px, -5px);
            transform: rotate(-45deg) translate(5px, -5px);
}

.mobile-menu .logo {
    width: 115px;
}

.mobile-options{
    width: 100vw;
    padding: 30px;
    display: -moz-flex;
    display: -ms-flex;
    display: -o-flex;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-orient: vertical;
    -webkit-box-direction: normal;
        -ms-flex-direction: column;
            flex-direction: column;
    -ms-align-items: center;
    -webkit-box-align: center;
        -ms-flex-align: center;
            align-items: center;
    position: absolute;
    z-index: 100;
    background-color: #fff;
    -webkit-transform: translateY(-150%);
            transform: translateY(-150%);
    -webkit-transition: .5s;
    transition: .5s;
    z-index: -1;
    top: 50px;
    left: 0
}

.mobile-options a {
    font-size: 16px;
    line-height: 32px;
    font-weight: bold;
    color: #3F5D45;
    cursor: pointer;
}

.desktop-menu{
    width: 940px;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-pack: justify;
        -ms-flex-pack: justify;
            justify-content: space-between;
    margin: 0 auto
}

@media (min-width: 1024px) {

    .desktop-menu .logo {
        width: 200px;
    }
}

.desktop-menu .menu {
    color: #3F5D45;
    font-size: 12px;
    letter-spacing: 2px;
    display: none;
}

@media (min-width: 1024px) {

    .desktop-menu .menu {
        display: -webkit-box;
        display: -ms-flexbox;
        display: flex;
    }
}

.desktop-menu .menu a {
    margin-left: 60px;
    display: block;
    position: relative;
    padding: 0 3px;
    -webkit-box-sizing: content-box;
            box-sizing: content-box;
    color: #3F5D45;
    line-height: 40px;
    font-size: 16px;
}

.desktop-menu .menu a i {
    margin-left: 20px;
}

.desktop-menu .menu a:after {
    content: '';
    position: absolute;
    background-color: #3F5D45;
    width: 0%;
    height: 3px;
    bottom: 0;
    left: 0;
    -webkit-transition: .3s;
    transition: .3s;
}

.desktop-menu .menu a:hover:after {
    width: 100%;
    -webkit-transition: .3s;
    transition: .3s;
}

.desktop-menu .menu a.nohover:after {
    width: 0;
}

.desktop-menu .menu a.active:after {
    width: 100%;
}

.mobile-options.open{
    -webkit-transform: translateY(0%);
            transform: translateY(0%);
}


