.shopping-stuff{
	margin: 30px 0;
	display: -webkit-box;
	display: -ms-flexbox;
	display: flex;
	-ms-flex-wrap: wrap;
	    flex-wrap: wrap;
	-webkit-box-pack: center;
	    -ms-flex-pack: center;
	        justify-content: center;
	-ms-align-items: center;
	-webkit-box-align: center;
	    -ms-flex-align: center;
	        align-items: center;
}
@media (min-width: 1024px){
	.shopping-stuff{
		-ms-flex-wrap: nowrap;
		    flex-wrap: nowrap;
		-webkit-box-pack: justify;
		    -ms-flex-pack: justify;
		        justify-content: space-between;
	}
}
.shopping-stuff .cover{
	width: calc(100% - 220px);
	max-width: 157px;
	height: 106px;
	background-size: cover;
	display: inline-block;
	margin-right: 16px;
}
@media (min-width: 1024px){
	.shopping-stuff .cover{
		max-width: unset;
		width: 110px;
		height: 110px;
		margin: 0;
	}
}
@media (min-width: 1024px){
	.shopping-stuff .content{
		display: -webkit-box;
		display: -ms-flexbox;
		display: flex;
	}
}
.shopping-stuff .content .stuff-title{
	margin-bottom: 8px;
}
@media (min-width: 1024px){
	.shopping-stuff .content .stuff-title{
		margin-right: 40px;
	}
}
.shopping-stuff .content .stuff-title p{
	color: #3F5D45;
	font-size: 20px;
	line-height: 25px;
}
.shopping-stuff .content .stuff-title .num{
	font-size: 16px;
}
.shopping-stuff .content .quantity-wrapper{
	border-collapse: collapse;
	display: -webkit-box;
	display: -ms-flexbox;
	display: flex;
}
.shopping-stuff .content .quantity-wrapper .click{
	cursor: pointer;
}
.shopping-stuff .content .quantity-wrapper .quantity-box{
	width: 48px;
	height: 48px;
	border: 1px solid #EAF0ED;
	text-align: center;
}
.shopping-stuff .content .quantity-wrapper .quantity-box span{
	line-height: 48px;
	font-size: 16px;
	color: #3F5D45;
}
.shopping-stuff .total{
	margin: 8px 0;
	padding: 8px 30px;
	width: 100%;
	border-top: 1px solid #EAF0ED;
	border-bottom: 1px solid #EAF0ED;
	text-align: right;
}
@media (min-width: 1024px){
	.shopping-stuff .total{
		width: auto;
		border: none;
		margin: 0;
		padding: 0;
	}
}
.shopping-stuff .total span{
	color: #3F5D45;
	font-size: 20px;
	font-weight: bold;
}
.shopping-stuff .delete i{
	color: #3F5D45;
	font-size: 16px;
	cursor: pointer;
}


