.page-sucessful .section-content{
    height: calc(100vh - 80px);
    width: 100%;
    background-size: cover;
    position: relative;
}
@media (min-width: 768px){
    .page-sucessful .section-content{
        padding: 0 50px;
        height: 460px;
    }
}
@media (min-width: 1024px){
    .page-sucessful .section-content{
        width: 940px;
        padding: 0;
        margin: auto;
        margin-bottom: 60px;
    }
}
.page-sucessful .section-content .panel{
    position: absolute;
    left: 50%;
    top: 50%;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
}
@media (min-width: 768px){
    .page-sucessful .section-content .panel{
        position: relative;
        top: 130px;
        left: auto;
        -webkit-transform: translate(0, 0);
                transform: translate(0, 0);
    }
}
.page-sucessful .section-content .panel .processbar{
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-pack: center;
        -ms-flex-pack: center;
            justify-content: center;
    -ms-align-items: center;
    -webkit-box-align: center;
        -ms-flex-align: center;
            align-items: center;
    margin-bottom: 30px;
}
.page-sucessful .section-content .panel .processbar .circle{
    width: 20px;
    height: 20px;
    border-radius: 50%;
    position: relative;
}
@media (min-width: 1024px){
    .page-sucessful .section-content .panel .processbar .circle{
        width: 20px;
        height: 20px;
    }
}
.page-sucessful .section-content .panel .processbar .stright{
    width: 60px;
    height: 1px;
    background-color: #3F5D45;
}
.page-sucessful .section-content .panel .processbar .circle.sucess{
    background-color: #3F5D45;
}
.page-sucessful .section-content .panel .processbar .circle.sucess:before, .page-sucessful .section-content .panel .processbar .circle.sucess:after{
    content: '';
    height: 2px;
    background-color: #EAF0ED;
    position: absolute;
    left: 50%;
    top: 50%;
}
.page-sucessful .section-content .panel .processbar .circle.sucess:before{
    width: 6px;
    -webkit-transform: rotate(45deg) translate(-4px, 5px);
            transform: rotate(45deg) translate(-4px, 5px);
}
.page-sucessful .section-content .panel .processbar .circle.sucess:after{
    width: 10px;
    -webkit-transform: rotate(-45deg) translate(-2px, -3px);
            transform: rotate(-45deg) translate(-2px, -3px);
}
.page-sucessful .section-content .panel .sucessful-word{
    width: 189px;
    margin: auto;
}
.page-sucessful .section-content .btn{
    width: 100%;
    height: 65px;
    background-color: #FFE180;
    display: block;
    position: absolute;
    bottom: 0;
    text-align: center;
    cursor: pointer;
}
@media (min-width: 768px){
    .page-sucessful .section-content .btn{
        position: relative;
        top: 181.64px;
        bottom: auto;
        width: 300px;
        margin: auto;
    }
}
.page-sucessful .section-content .btn span{
    color: #3F5D45;
    font-size: 24px;
    line-height: 65px;
    font-weight: bold;
}




